<style lang="less">
@baseColor: #ffc124;
.main_wrap{
	position: relative;
	.official_about{
		right: 38px;
		bottom: 35px;
	}
}
.title{
		display: flex;
		justify-content: center;
		align-items: center; 
		height: 105px;
		font-size: 22px;
	}
.bd_main{
	margin-top: 0;
	display: flex;
	justify-content: center;
	position: relative;
	.left_btn_wrap,
	.right_btn_wrap{
		display: flex;
    	align-items: center;
    	height: 450px;
    	margin: 0 30px;
	}
	.left_btn_wrap a{
		height: 28px;
		width: 15px;
		background: url('../../assets/img/detail_sprite.png') -144px -0px no-repeat;
	}
	.right_btn_wrap a{
		height: 28px;
		width: 15px;
		background: url('../../assets/img/detail_sprite.png') -159px -0px no-repeat;
	}
	.swiper_wrap{
		display: flex;
		justify-content: center;
		align-items: center;
		width: 720px;
		height: 450px;
		border: 1px solid #c8c8c8;
		border-radius: 10px;
		margin-bottom: 130px;
		overflow: hidden;
		.swiper{
			width: 700px;
			height: 430px;
			.swiper-pagination .swiper-pagination-bullet{
				width: 13px;
				height: 13px;
				&.active{
					background: #ffa626;
				}
			}
		}
	}
	.left_aside,
	.right_aside{
		display: flex;
    	flex-direction: column;
    	a{
    		display: flex;
    		justify-content: center;
    		align-items: center;
    		flex-direction: column;
    		width: 60px;
    		height: 60px;
    		margin-bottom: 18px;
    	}
    	span{
    		color: #ff6e52;
    	}
    	.icon{
    		display: block;
			height: 31px;
			width: 36px;
			margin-bottom: 10px;
    	}
	}
	.left_aside{
		.icon_tranefer{
			background: url('../../assets/img/detail_sprite.png') -37px -0px no-repeat;
		}
		.icon_msgerror{
			width: 37px;
			height: 38px;
			background: url('../../assets/img/detail_sprite.png') -0px -0px no-repeat;
		}
	}
	.right_aside{
		.icon_collect{
			background: url('../../assets/img/detail_sprite.png') -110px -0px no-repeat;
		}
		.icon_share{
			background: url('../../assets/img/detail_sprite.png') -73px -0px no-repeat;
		}
	}
}
.goods_info{
	display: flex;
	justify-content: center;
	.good{
		width: 950px;
		background: #fff;
		display: flex;
		justify-content: center;
		flex-direction: row;
		padding: 35px 20px;
		margin-bottom: 28px;
		.img_wrap{
			padding: 5px;
			border: 1px solid #c8c8c8;
			margin-right: 10px;
			img{
				width: 90px;
				height: 90px;
			}
		}
		&>span{
			width: 90px;
			height: 90px;
			background: #ecf0f1;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 15px;
		}
		.info{
			width: 400px;
			display: flex;
			flex-wrap: wrap;
			.seller, 
			.qq,
			.time{
				margin-right: 15px;
				display: flex;
				justify-content: center;
				align-items: center;
			}
			.border{
				padding: 5px 15px;
				border: 1px solid #d8d8d8;
				display: flex;
				justify-content: center;
				align-items: center;
				margin-right: 25px;
				height: 30px;
			}
		}
	}
}
.good_detail{
	display: flex;
	justify-content: center;
	.detail{
		width: 950px;
		background: #fff;
		padding: 35px 20px;
		margin-bottom: 28px;
	}
}
.comment_wrap{
	display: flex;
	justify-content: center;
	.comments{
		width: 950px;
		background: #fff;
		padding: 35px 20px;
		margin-bottom: 28px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		.comment_area{
			textarea{
				width: 880px;
				height: 125px;
				border: 1px solid #d2d2d2;
				padding: 20px 28px;
			}
			.comment_btn{
				width: 105px;
				height: 28px;
				color: #fff;
				background: #ffa626;
				display: flex;
				justify-content: center;
				align-items: center;
				float: right;
			}
			.note{
				color: #999;
				margin-left: 20px;
			}
		}
		.comment,
		.replys li{
			display: flex;
			flex-direction: row;
			margin-bottom: 20px;
		}
		.img_wrap{
			width: 90px;
			height: 90px;
			padding: 5px;
			border: 1px solid #c8c8c8;
			margin-right: 25px;
			img{
				height: 100%;
			}
		}
		.content_wrap{
				.name{
					margin-bottom: 15px;
				}
				.content{
					width: 700px;
					height: 80px;
					padding: 10px;
					border: 1px dotted #c4c4c4;
					position: relative;
					.time{
						position: absolute;
						bottom: 10px;
						left: 10px;
					}
				}
			}
		.replys{
			display: flex;
    		flex-direction: row-reverse;
    		.content_wrap{
				.content{
					width: 572px;
				}
			}
		}
	}
}
</style>

<template>
	<ui-header></ui-header>
	<div v-if="fetchGoodInfoState === 'resolve'" class="main_wrap">
	<div class="title">{{info.title}}</div>
	<div class="bd_main">
		<div class="left_aside">
			<a href="javascript:void(0)">
				<span class="icon icon_tranefer"></span>
				<span class="text">出手转让</span>
			</a>
			<a href="javascript:void(0)">
				<span class="icon icon_msgerror"></span>
				<span class="text">信息有误</span>
			</a>
		</div>
		<div class="left_btn_wrap"><a href="" @click.prevent="leftClick"></a></div>
		<div class="swiper_wrap">
			<swiper v-ref:swiper
			        direction="horizontal"
			        :mousewheel-control="true"
			        :performance-mode="false"
			        :pagination-visible="true"
			        :pagination-clickable="true"
			        :loop="true"
			        @slide-change-start="onSlideChangeStart"
			        @slide-change-end="onSlideChangeEnd">
			    <img v-for="its in info.imageList" v-bind:src="its.informationimage" width="100%" height="100%">
			</swiper>
		</div>
		<div class="right_btn_wrap"><a href="" @click.prevent="rightClick"></a></div>
		<div class="right_aside">
			<a href="" @click.prevent="collect">
				<span class="icon icon_collect"></span>
				<span class="text">收藏</span>
			</a>
			<a href="javascript:void(0)">
				<span class="icon icon_share"></span>
				<span class="text">分享</span>
			</a>
		</div>
	</div>
	<div class="goods_info">
		<div class="good">
			<div class="img_wrap">
				<img :src="info.userinfo.userimg">
			</div>
			<div class="info">
				<span class="seller">卖家</span><span class="border">{{info.userinfo.username}}</span>
				<span class="qq">QQ</span><span class="border">{{info.userinfo.qq}}</span>
				<span class="border">已认证</span>
				<span class="border">等级{{info.userinfo.level}}</span>
				<span class="time">发布日期</span><span class="border">{{info.createtime}}</span>
			</div>
			<span>{{info.address}}</span>
			<span>{{info.isbargain ? '不议价' : '可议价'}}</span>
			<span>{{info.isexchange ? '不置换' : '可置换'}}</span>
			<span>￥{{info.exprice}}</span>
		</div>
	</div>
	<div class="good_detail">
		<div class="detail">{{info.informationdes}}</div>
	</div>
	<div class="comment_wrap">
		<div class="comments">
			<ul v-if="commentList.length > 0">
				<li v-for="its in commentList">
					<div class="comment">
						<div class="img_wrap">
							<img :src="its.portrait" />
						</div>
						<div class="content_wrap">
							<div class="name">{{its.username}}(买家)</div>
							<div class="content">
								{{its.commentcontent}}
								<span class="time">{{its.commenttime}}</span>
							</div>
						</div>
					</div>
					<ul class="replys" v-for="it in its.replys">
						<li>
							<div class="img_wrap">
								<img :src="it.portrait" />
							</div>
							<div class="content_wrap">
								<div class="name">{{it.username}}(回复者)</div>
								<div class="content">
									@{{its.username}}：{{it.replycontent}}
									<span class="time">{{it.replytime}}</span>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
			<div class="comment_area">
				<textarea placeholder="140字以内" id="commentcontent" @input="getCommentNum"></textarea>
				<div class="note">剩余<span>{{commentnum}}</span>个字</div>
				<a class="comment_btn" href="" @click.prevent="submitComment">评论</a>
			</div>
		</div>
	</div>
	<ui-Official></ui-Official>
	</div>
	<ui-footer></ui-footer>
</template>

<script>
import uiHeader from '../../components/header'
import uiFooter from '../../components/footer'
import uiOfficial from '../../components/official'
import Swiper from './vue-swiper'

export default {
	data() {
		return{
			isLogin: false,
			fetchGoodInfoState: 'pending',
			info: {},
			currentPage: 1,
			commentnum: 140, //当前评论剩余数字
			commentList: [{
					userid: 1,
					username: '小王',
					portrait: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1492870446156&di=026443bd6939d225b5c127ba020c2835&imgtype=0&src=http%3A%2F%2Fwww.qqxoo.com%2Fuploads%2Fallimg%2F170421%2F2335353C8-8.jpg',
					commentcontent: '便宜10元可以吗？',
					commenttime: '2016-12-09',
					replys: [{
						userid: 3,
						username: '小卖家',
						portrait: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1492870591397&di=b014a74f78500fa9cbaa1ec7fb7df700&imgtype=0&src=http%3A%2F%2Fb.hiphotos.baidu.com%2Fzhidao%2Fwh%253D450%252C600%2Fsign%3Df0c5c08030d3d539c16807c70fb7c566%2F8ad4b31c8701a18bbef9f231982f07082838feba.jpg',
						replycontent: '该商品不议价',
						replytime: '2016-12-09',
					}]
				}, {
					userid: 2,
					username: '小李',
					portrait: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1492870591398&di=a0a26fe71129f98b79f8d35ddea19eb0&imgtype=0&src=http%3A%2F%2Fwww.qqxoo.com%2Fuploads%2Fallimg%2F170421%2F233535N52-12.jpg',
					commentcontent: '这个有红色的吗？',
					commenttime: '2016-12-10',
					replys: [{
						userid: 3,
						username: '小卖家',
						portrait: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1492870591397&di=b014a74f78500fa9cbaa1ec7fb7df700&imgtype=0&src=http%3A%2F%2Fb.hiphotos.baidu.com%2Fzhidao%2Fwh%253D450%252C600%2Fsign%3Df0c5c08030d3d539c16807c70fb7c566%2F8ad4b31c8701a18bbef9f231982f07082838feba.jpg',
						replycontent: '不好意思，没有',
						replytime: '2016-12-11',
					}]
			}]
		}
	},
	components: {
		uiHeader,
		uiFooter,
		uiOfficial,
		Swiper
	},
	ready() {
		const goodId = this.$route.params.id;
		this.fetchGoodInfo(goodId);
		const isLogin = localStorage.getItem('userLogin');
		if(isLogin) {
			this.isLogin = isLogin;
		}
	},
	methods: {
		fetchGoodInfo(id) {
			const info = require('../../../mock/goodinformation').default.data;
			fetch("http://xianyijun.cn:8080/information/" + id).then((res) => {
				return res.json()
			}).then(data => {
				this.fetchGoodInfoState = 'resolve';
				this.info = data.data;
			});
			// this.fetchGoodInfoState = 'resolve';
			// this.info = info;
		},
		//轮播左箭头
		leftClick() {
			// currentPage取值1,2,3
			let swiperIdx = 0;
			if(this.currentPage === 1) {
				swiperIdx = this.info.imageList.length - 1;
			} else {
				swiperIdx = this.currentPage - 2;
			}
			document.querySelectorAll('.swiper-pagination span')[swiperIdx].click();
		},
		//轮播右箭头
		rightClick() {
			let swiperIdx = 0;
			if(this.currentPage === this.info.imageList.length) {
				swiperIdx = 0;
			}else {
				swiperIdx = this.currentPage;
			}
			document.querySelectorAll('.swiper-pagination span')[swiperIdx].click();
		},
		onSlideChangeStart (currentPage) {
			this.currentPage = currentPage;
		},
		onSlideChangeEnd (currentPage) {
			this.currentPage = currentPage;
		},
		//收藏
		collect() {
			const goodId = this.$route.params.id;
			//发请求
			const res = require('../../../mock/collectres').default;
			if(res.code === 1) {
				alert('收藏成功');
			} else if(res.code === -1){
				alert('您已经收藏过啦');
			}

		},
		//评论
		submitComment() {
			if(!this.isLogin) {
				alert('请先登录');
				return;
			}
			const content = document.querySelector('#commentcontent').value;
			if(!content){
				alert('您没有填写评论');
				return;
			}else {
				if(this.commentnum>140) {
					alert('您的评论字数已超过限制字数！');
					return;
				}
				this.commentList.push({
					userid: 3,
					username: '小张',
					portrait: 'https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=3647792746,2268622560&fm=111&gp=0.jpg',
					commentcontent: content,
					commenttime: new Date().toISOString().match(/(\d{4}-\d{2}-\d{2})/)[1],
					replys: []});
			}
		},
		//获取评论区剩余可输入字数
		getCommentNum(){  
		 	this.commentnum = 140 - document.querySelector('#commentcontent').value.length;
		}  
	}
}
</script>


